<template>
  <div class="navBar-container">
    <div class="content">
      <ul class="menuList-small">
        <li class="menuIcon-nonactivated" @click="handleClickNonAct">=</li>
        <li class="menuIcon-activated" @click="handleClickAct">+</li>
        <li class="Logo"></li>
        <li class="title"></li>
      </ul>
      <ul class="menuList-big">
        <li
          v-for="(item, index) in menuList"
          :key="index"
          @click="handleSelected($event)"
        >
          <!-- <a href="">{{ ele.name }}</a> -->
          <Router-link :to="{ name: item.name }">
            <span>{{ item.title }}</span>
          </Router-link>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      menuList: [
        { name: "Home", title: "Home" },
        { name: "Store", title: "Store" },
        { name: "Mac", title: "Mac" },
        { name: "iPad", title: "iPad" },
        { name: "Watch", title: "Watch" },
        { name: "AirPods", title: "AirPods" },
        { name: "TV & Home", title: "TV & Home" },
        { name: "Accessories", title: "Accessories" },
        { name: "Support", title: "Support" },
      ],
    };
  },
  // opacity: 0;
  // visibility: hidden;
  methods: {
    handleClickNonAct() {
      const navBarContainer =
        document.getElementsByClassName("navBar-container")[0];
      const menuListBig = document.getElementsByClassName("menuList-big")[0];
      navBarContainer.classList.add("small-activated");
      menuListBig.classList.add("small-activated");
      const nonactivated = document.getElementsByClassName(
        "menuIcon-nonactivated"
      )[0];
      // nonactivated.style.display = "none";
      nonactivated.style.opacity = 0;
      nonactivated.style.visibility = "hidden";
      const activated =
        document.getElementsByClassName("menuIcon-activated")[0];
      // activated.style.display = "block";
      activated.style.opacity = 1;
      activated.style.visibility = "visible";
    },
    handleClickAct() {
      const nonactivated = document.getElementsByClassName(
        "menuIcon-nonactivated"
      )[0];
      // nonactivated.style.display = "block";
      nonactivated.style.opacity = 1;
      nonactivated.style.visibility = "visible";
      const activated =
        document.getElementsByClassName("menuIcon-activated")[0];
      // activated.style.display = "none";
      activated.style.opacity = 0;
      activated.style.visibility = "hidden";
      const navBarContainer =
        document.getElementsByClassName("navBar-container")[0];
      const menuListBig = document.getElementsByClassName("menuList-big")[0];
      navBarContainer.classList.remove("small-activated");
      menuListBig.classList.remove("small-activated");
    },
    handleSelected(info) {
      //处理用户选择菜单中某一项之后，菜单栏自动消失
      console.log(info.target);
      const navBarContainer =
        document.getElementsByClassName("navBar-container")[0];
      navBarContainer.classList.remove("small-activated");
      const menuListBig = document.getElementsByClassName("menuList-big")[0];
      menuListBig.classList.remove("small-activated");

      const activated =
        document.getElementsByClassName("menuIcon-activated")[0];
      // activated.style.display = "none";
      activated.style.opacity = 0;
      activated.style.visibility = "hidden";

      const nonactivated = document.getElementsByClassName(
        "menuIcon-nonactivated"
      )[0];
      // nonactivated.style.display = "block";
      nonactivated.style.opacity = 1;
      nonactivated.style.visibility = "visible";
    },
  },
};
</script>

<style lang="less" scoped>
.menuList-small {
  display: none;
}
.navBar-container {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9999;
  display: block;
  margin: 0;
  width: 100%;
  height: 48px;
  max-height: 44px;
  font-size: 17px;
  background: rgba(26, 26, 26, 1);

  .content {
    display: block;
    margin: 0 auto;
    max-width: 980px;
    padding: 0 22px;
    position: relative;
    z-index: 2;
    padding-left: max(22px, env(safe-area-inset-left));
    padding-right: max(22px, env(safe-area-inset-right));
  }
  .menuList-big {
    display: flex;
    height: 44px;
    width: auto;
    justify-content: space-between;
    list-style-type: none;
    li {
      box-sizing: border-box;
      display: inline-block;
      position: relative;
      height: 44px;
      z-index: 1;
      vertical-align: top;
      line-height: 44px;

      a {
        display: block;
        text-decoration: none;
        color: rgb(155, 155, 155);
      }
    }
  }
}
@media only screen and (max-width: 480px) {
  .navBar-container {
    max-height: none;
    height: 48px;
    transition: height 0.5s cubic-bezier(0.32, 0.08, 0.24, 1); //黑色背景效果
    &.small-activated {
      max-height: none;
      width: 100%;
      height: 100%;
      background: black; //after click
      transition: height 0.5s cubic-bezier(0.32, 0.08, 0.24, 1); //黑色背景效果
    }
  }
  // .navBar-container.small-activated {
  //   max-height: none;
  //   width: 100%;
  //   height: 100%;
  //   background: black; //after click
  //   transition: height 0.5s cubic-bezier(0.32, 0.08, 0.24, 1);
  // }
  .navBar-container .content {
    position: relative;
    padding: 0;
  }

  .menuList-small {
    position: absolute;
    display: flex;
    width: 100%;
    height: 48px;
    top: 0;
    left: 0;
    z-index: 5;
    list-style: none;

    .menuIcon-nonactivated {
      position: absolute;
      font-size: 35px;
      color: white;
      padding-top: 4px;
      padding-left: 8px;
      transition: opacity 0.4s cubic-bezier(0.32, 0.08, 0.24, 1); //左边按钮效果
    }
    .menuIcon-activated {
      opacity: 0;
      visibility: hidden;
      // display: none;
      position: absolute;
      font-size: 35px;
      color: white;
      padding-top: 0px;
      padding-left: 10px;
      transform: rotate(45deg);
      transition: opacity 0.4s cubic-bezier(0.32, 0.08, 0.24, 1); //左边按钮效果
    }
  }

  .navBar-container .menuList-big {
    top: 48px;
    bottom: 0;
    right: 0;
    left: 0;
    max-width: 664px;
    box-sizing: border-box;
    display: block;
    opacity: 0;
    transition: opacity 0.4s cubic-bezier(0.32, 0.08, 0.24, 1); //menu里文字效果-关闭时
    li {
      z-index: 1;
      pointer-events: none;
      // 1/2行解决z-index上层元素遮挡下层元素点击事件问题 li 遮挡 a 元素
      display: block;
      padding: 45px 40px 8px;

      a {
        z-index: 2;
        pointer-events: auto;
        // 1/2行解决z-index上层元素遮挡下层元素点击事件问题
        color: rgb(155, 155, 155);
        text-decoration: none;
        border-bottom: 1px solid grey;
      }
    }
    &.small-activated {
      opacity: 1;
      transition: opacity 0.8s cubic-bezier(0.32, 0.08, 0.24, 1); //menu里文字效果-点开时
    }
  }
}
</style>